<template>
<div class="container">
  <Notice ref="notice"> </Notice>

  
  <div class="container" style="margin-botton:30px">
    <h4 style="border-bottom: 1px solid gray"> Wi-Fi Connection </h4>
    <p> <span class="title">SSID</span>: {{connStatus.ssid}} </p>
    <p> <span class="title">Status</span>: {{connStatus.wpa_state}} </p>
    <p> <span class="title">Security</span>: {{connStatus.key_mgmt}} </p>
    <p> <span class="title">Frequency</span>: {{connStatus.freq}} </p>
  </div>

  <table class="pure-table">
    <thead>
        <tr>
            <th>SSID</th>
            <th>Security</th>
            <th>Strength</th>
        </tr>
    </thead>
    <tbody>
    <tr v-for="hotspot in hotspots" :key="hotspot.ssid"  @click="onClickHotspot(hotspot.ssid, hotspot.security)" v-bind:class="[hotspot.status]">
      <td> {{hotspot.ssid}} </td>
      <td> {{hotspot.security}} </td>
      <td>
        <div v-bind:class="[hotspot.strength]" style="text-align: center; margin-bottom:-12px">
          <div class="wv4 wave" style="">
          <div class="wv3 wave" style="">
          <div class="wv2 wave" style="">
          <div class="wv1 wave">
          </div>
          </div>
          </div>
          </div>
        </div>
      </td>
    </tr>
    </tbody>
  </table>

  <div v-if="!isHidden" class="modal-window">
  <div>
  <form class="pure-form pure-form-aligned">
    <fieldset>
        <legend>Connect to Wi-Fi</legend>
	<div class="pure-control-group">
          <label for="ssid">SSID</label>
          <input type="text" id="ssid" name="ssid" :placeholder="ssid" disabled=""/>
        </div>
	<div class="pure-control-group">
          <label for="password">Password</label>
          <input type="password" id="password" name="password" placeholder="Password" v-model="password" />
        </div>
	<div class="pure-control-group">
          <label for="security">Security</label>
          <input type="text" id="security" name="security" :placeholder="security" disabled=""/>
        </div>
	<div class="pure-controls">
          <button v-on:click="onClickSubmit()" type="button" class="pure-button pure-button-primary">Submit</button> 
          <button v-on:click="isHidden=true" class="pure-button pure-button-primary" style="margin-left:20px">Cancel</button>
        </div>
    </fieldset>
  </form>
  </div>
  </div>

</div>
</template>

<script>

import Notice from './Notice.vue'

export default {
  name: 'Network',
  components: {
    Notice,
  },
  props: {
  },
  data() {
    return {
      isHidden: true,
      isLoading: false,
      connStatus: {},
      ssid: "",
      security: "",
      password: "",
      hotspots: [],
    }
  },
  created() {
    this.$http.get('/api/v1/network/wireless/connection', {})
      .then((response) => {
        this.connStatus = response.data;
      })
      .catch((error) => {
        console.log(error);
      })
      .then(() => {
        //this.isLoading = false;
      });
      this.scan();
  },
  methods: {
    scan() {

      //this.isLoading = true;
      this.$http.get('/api/v1/network/hotspot', {})
        .then((response) => {
          this.hotspots = response.data;
          for(let i = 0; i < this.hotspots.length; i++) {
            this.hotspots[i].strength = "waveStrength" + this.hotspots[i].strength;
            if(this.hotspots[i].ssid == this.connStatus.ssid) {
              this.hotspots[i].status = "associated";
            }
          }
        })
        .catch((error) => {
          console.log(error);
        })
        .then(() => {
          //this.isLoading = false;
        });
    },
    onClickHotspot(ssid, security) {
      this.isHidden = false;
      this.ssid = ssid;
      this.security = security;
    },
    onClickSubmit() {
      this.isHidden = true;
      this.isLoading = true;

      this.$http.post('/api/v1/network/wireless/connection', {
        ssid: this.ssid,
        psk: this.password,
        security: this.security,
      })
      .then((response) => {
        if(response.status == 200) {
          this.$refs.notice.showMsg("Apply successfully");
        }

      })
      .catch((error) => {
        if (error.response) {
          this.$refs.notice.showMsg(error.response.data);
        }
      })
      .then(() => {
        this.isLoading = false;
      });


    }
  },
}
</script>

<style>


.associated {
  background-color: #cfd8dc;
}

tr:hover {
  background: #cfd8dc;
  outline: none;
  cursor: pointer;
}

.title {
  display:inline-block;
  width:80px;
}


</style>
