<template>
<div>
  <form class="pure-form pure-form-stacked">
      <fieldset>
      <legend><img class="text-icon" src="/img/vpn_key-black-18dp.svg"> Password</legend>
        <div class="pure-control-group">
          <label for="aligned-name">Username</label>
          <input type="text" disabled="" id="aligned-name" placeholder="admin" />
        </div>
        <div class="pure-control-group">
          <label for="aligned-password">Password</label>
          <input type="password" id="password1" placeholder="Password" v-model="password1" />
        </div>
        <div class="pure-control-group">
          <label for="aligned-password"></label>
          <input type="password" id="password2" placeholder="Password again" v-model="password2" />
        </div>
        <div class="pure-controls">
          <button v-on:click="onClickSetting('Update password', submit)" type="button" class="pure-button pure-button-primary">Submit</button>
        </div>
    </fieldset>
  </form>

  <form class="pure-form pure-form-stacked">
    <fieldset>
      <legend><img class="text-icon" src="/img/exit_to_app-black-18dp.svg"> Logout</legend>
      <button type="button" v-on:click="onClickSetting('Logout', logout)" class="pure-button pure-button-primary">Logout</button>
    </fieldset>
  </form>

  <form class="pure-form pure-form-stacked">
    <fieldset>
      <legend><img class="text-icon" src="/img/sync-black-18dp.svg"> Reboot</legend>
      <button type="button" v-on:click="onClickSetting('Reboot', reboot)" class="pure-button pure-button-primary">Reboot</button>
    </fieldset>
  </form>


  <div v-if="!isHidden" class="modal-window">
    <div style="text-align: center;">
      <h3>{{msg}}</h3>
    </div>
  </div> 

  <div v-if="!confirmIsHidden" class="modal-window">
    <div style="text-align: center;">
    <h4> {{message}}. Are you sure? </h4>
    <button v-on:click="onClickYes" class="pure-button pure-button-primary">Yes</button>
    <button v-on:click="confirmIsHidden=true" class="pure-button pure-button-primary" style="margin-left:20px">No</button>
    </div>
  </div>


</div>
</template>

<script>

export default {
  name: 'Setting',
  props: {
  },
  data() {
    return {
      message: "",

      password1: "",
      password2: "",
      confirmIsHidden: true,
      isHidden: true,
      msg: "",
    };
  },
  created() {
  },
  methods: {
    submit() {

      this.$http.put('/api/v1/account/admin', {
        password1: this.password1,
        password2: this.password2,
      })
      .then((response) => {
        if(response.status == 200) {
          this.msg = "Update successfully";
        }

      })
      .catch((error) => {
        if (error.response) {
          this.msg = error.response.data;
        }
      })
      .then(() => {
        this.isHidden = false;
        setTimeout(() => { this.isHidden = true; }, 2000);
      });
    },
    onClickSetting(message, callback) {
      this.confirmIsHidden = false;
      this.message = message;
      this.action = callback;
    },
    onClickYes() {
      this.action();
      this.confirmIsHidden = true;
    },
    reboot() {
      this.$http.post('/api/v1/system/command/reboot', {},
      )
      .then(() => {
        this.logout();
      })
      .catch(() => {
      })
      .then(() => {
      });
    },
    logout() {
      localStorage.removeItem("xiaoPiToken");
      sessionStorage.removeItem("xiaoPiToken");
      location.reload();
    }

  
  },
}
</script>

<style scoped>
legend {
  border-bottom: 1px solid gray;
  font-weight: bold;
}

form {
  padding-bottom: 30px;
}
</style>
