<template>
<div>

  <form class="pure-form" style="padding-top:30px;">
    <fieldset>
      <legend>Enable Remote Connection</legend>
      <label class="switch">
      <input type="checkbox" v-model="checked" v-on:change="remoteEnable()">
      <span class="slider round"></span>
      </label>
    </fieldset>
  </form>

  <div v-show="!isHidden">
    <form class="pure-form" style="padding-top:30px">
      <fieldset>
        <legend>Your Device Key (Must be 6-12 char) </legend>
        <input style="margin-right:30px;" type="text" v-model="deviceKey" />
        <button type="button" v-on:click="updateDeviceKey" class="pure-button pure-button-primary">Update</button>
      </fieldset>
    </form>

    <form class="pure-form" style="padding-top:30px">
      <fieldset>
        <legend> WebRTC Link </legend>
        <a v-bind:href="deviceUrl">
        <div id="qrcode"></div>
        </a>
      </fieldset>
    </form>
  </div>


  <div v-if="!isUpdate" class="modal-window">
    <div style="text-align: center;">
      <h3>{{msg}}</h3>
    </div>
  </div>

  <footer v-if="hasNewSetting">
    <p>Reboot your Pi Camera for new setting</p>
  </footer>

</div>
</template>

<script>
import QRCode from 'qrcodejs2'
export default {
  name: 'Remote',
  props: {
  },
  created() {
    this.$http.get('/api/v1/system/remote/enable', {})
      .then((response) => {
        this.checked = response.data["remoteEnable"];
        if(this.checked == true) {
          this.isHidden = false;
        }
        else {
          this.isHidden = true;
        }

      })
      .catch((error) => {
        console.log(error);
      })
      .then(() => {
      });

    this.$http.get('/api/v1/system/remote/deviceKey', {})
      .then((response) => {
        this.deviceKey = response.data["deviceKey"];
      })
      .catch((error) => {
        console.log(error);
      })
      .then(() => {
      });

    this.$http.get('/api/v1/system/remote/deviceCode', {})
      .then((response) => {
        this.deviceCode = response.data["deviceCode"];
        this.deviceUrl = "https://xiaopi.me/device/" + this.deviceCode;
        this.qrcode();
      })
      .catch((error) => {
        console.log(error);
      })
      .then(() => {
      });


  },
  data() {
    return {
      checked: false,
      deviceKey: "",
      deviceCode: "",
      isHidden: true,
      isUpdate: true,
      hasNewSetting: false,
      deviceUrl: "",
      msg: "",
    }
  },
  methods: {
    updateDeviceKey() {
      this.$http.put('/api/v1/system/remote/deviceKey', {
        deviceKey: this.deviceKey,
      })
      .then((response) => {
        if(response.status == 200) {
          this.msg = "Update successfully";
          this.hasNewSetting = true;
        }

      })
      .catch((error) => {
        if (error.response) {
          this.msg = error.response.data;
        }
      })
      .then(() => {
        this.isUpdate = false;
        setTimeout(() => { this.isUpdate = true; }, 2000);
      })

    },
    remoteEnable() {

      this.$http.put('/api/v1/system/remote/enable', {
        remoteEnable: this.checked,
      })
      .then(() => {
        this.hasNewSetting = true;
      })
      .catch(() => {
      })
      .then(() => {
        if(this.checked == true) {
          this.isHidden = false;
        }
        else {
          this.isHidden = true;
        }
      });
    },
    qrcode() {
      new QRCode('qrcode', {  
        width: 300,  
        height: 300,
        text: this.deviceUrl,
      })
    },
  },
  mounted() {
  },

}

</script>

<style scoped>

.center {
  display: flex;
  justify-content: center;
  align-items: center;
}

a.pure-menu-link.title::after {
  content: "";
  
}

ul.pure-menu-children {
  border: 1px solid #009688;
}

.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.switch input { 
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

footer {

  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  text-align: center;
  background-color:#0078e7;
  color:#fff
}

</style>
