<template>
<div v-if="!isHidden" class="modal-window">
  <div>
    <form class="pure-form pure-form-aligned">
      <legend>Sign In</legend>
      <fieldset>
        <div class="pure-control-group">
          <label for="aligned-name">Username</label>
          <input class="pure-input-1-2" type="text" placeholder="Username" v-model="username"/>
        </div>
        <div class="pure-control-group">
          <label for="aligned-password">Password</label>
          <input class="pure-input-1-2" type="password" placeholder="Password" v-model="password" />
        </div>
        <p style="color: red">{{loginMsg}}</p>
        <div class="pure-controls">
          <label for="aligned-cb" class="pure-checkbox">
          <input type="checkbox" id="aligned-cb" v-model="checkbox" /> Remember me</label>
          <button v-on:click="signIn()" type="button" class="pure-button pure-button-primary">Sign In</button>
        </div>
      </fieldset>
    </form> 
  </div>  
</div>
</template>

<script>


export default {
  name: 'Login',
  props: {
  },
  created() {

    if(sessionStorage.getItem("xiaoPiToken") != undefined || localStorage.getItem("xiaoPiToken") != undefined) {
      this.isHidden = true;
    }
  },
  data() {
    return {
      username: "",
      password: "",
      checkbox: false,
      isHidden: false,
      loginMsg: "",
    };
  },
  methods: {
    signIn() {
        this.$http.post('/api/v1/account/login', {
            username: this.username,
            password: this.password,
        })
        .then((response) => {
          if(response.status == 200) {
            this.isHidden = true;
            if(this.checkbox == true)
              localStorage.setItem("xiaoPiToken", response.data);
            else
              sessionStorage.setItem("xiaoPiToken", response.data);
            location.reload();
          }
        })
        .catch((error) => {
            console.log(error);
          if (error.response) {
            console.log(error.response);
            if(error.response.status == 401) {
              this.loginMsg = "Invalid username or password";
            }
          }
        })
        .then(function () {
        });
    },
    checkLogin() {
      
    }
  },
}




</script>

