import Vue from 'vue'
import App from './App.vue'
import Axios from 'axios'
import axios from 'axios'
Vue.config.productionTip = false

Vue.prototype.$http = Axios;

axios.interceptors.request.use(async function (config) {

  let token = "";
  if(sessionStorage.getItem("xiaoPiToken") != undefined)
    token = sessionStorage.getItem("xiaoPiToken");

  if(localStorage.getItem("xiaoPiToken") != undefined)
    token = localStorage.getItem("xiaoPiToken");

  config.headers.Authorization = token;
  return config
}, function (error) {
  return Promise.reject(error)
})

axios.interceptors.response.use(function (response) {
  return response
}, function (error) {
  if (error.response) {

    // server responded status code falls out of the range of 2xx
    switch (error.response.status) {
      case 401:
          localStorage.removeItem("xiaoPiToken");
          sessionStorage.removeItem("xiaoPiToken");
        break
    }
  } 
  return Promise.reject(error)
})


new Vue({
  render: h => h(App),
}).$mount('#app')
